import { newE2EPage } from '@stencil/core/testing';

describe('presto-cta-overlay-controller', () => {
  let page, element;

  beforeEach(async () => {
    page = await newE2EPage();
    await page.setContent('<presto-cta-overlay-controller></presto-cta-overlay-controller>');
    element = await page.find('presto-cta-overlay-controller');
  });

  it('renders', async () => {
    expect(element).toHaveClass('hydrated');
  });

  it('Should only show after time has passed', async () => {
    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.cta = {
        enabled: true,
        percentage: 10,
      };
      elm.currentTime = 0;
      elm.duration = 100;
    });

    await page.waitForChanges();

    let overlay = await page.find('presto-cta-overlay-controller presto-cta-overlay-ui');
    expect(overlay).toBe(null);

    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.currentTime = 11;
    });

    await page.waitForChanges();

    overlay = await page.find('presto-cta-overlay-controller presto-cta-overlay-ui');
    expect(overlay).not.toBe(null);

    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.cta = {
        enabled: true,
        percentage: 100,
      };
      elm.currentTime = 99.99; // check for rounding errors
    });

    overlay = await page.find('presto-cta-overlay-controller presto-cta-overlay-ui');
    expect(overlay).not.toBe(null);
  });

  it('Displays a skip or rewatch button', async () => {
    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.cta = {
        enabled: true,
        percentage: 0,
      };
      elm.currentTime = 0;
      elm.duration = 100;
    });

    await page.waitForChanges();

    const component = await page.find('presto-cta-overlay-controller presto-cta-overlay-ui');
    let skip = component.shadowRoot.querySelector('.skip');
    expect(skip).toBeDefined();

    let rewatch = component.shadowRoot.querySelector('.rewatch');
    expect(rewatch).toBeNull();

    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.cta = {
        enabled: true,
        percentage: 0,
        show_skip: true,
        show_rewatch: true,
      };
      elm.currentTime = 0;
      elm.duration = 100;
    });

    await page.waitForChanges();

    skip = component.shadowRoot.querySelector('.skip');
    expect(skip).toBeDefined();

    rewatch = component.shadowRoot.querySelector('.rewatch');
    expect(rewatch).toBeNull();

    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.cta = {
        enabled: true,
        percentage: 0,
        show_skip: true,
        show_rewatch: true,
      };
      elm.ended = true;
      elm.currentTime = 100;
    });

    await page.waitForChanges();

    skip = component.shadowRoot.querySelector('.skip');
    expect(skip).toBeNull();

    await page.$eval('presto-cta-overlay-controller', (elm: any) => {
      elm.cta = {
        enabled: true,
        percentage: 0,
        show_skip: true,
        show_rewatch: true,
      };
      elm.ended = true;
      elm.currentTime = 100;
    });

    await page.waitForChanges();

    skip = component.shadowRoot.querySelector('.skip');
    expect(skip).toBeNull();

    rewatch = component.shadowRoot.querySelector('.rewatch');
    expect(rewatch).toBeDefined();
  });
});
